.align-right {text-align: right;}
.align-left {text-align: left;}
.d-grid { display: grid;}
.grid-container {display: grid;gap:0.25rem;}
.grid-3-col {display:grid;grid-template-columns: repeat(3,1fr);gap:0 1rem;}
.grid-2-col {display:grid;grid-template-columns: repeat(2,1fr);gap:0 1rem;}
.grid-1-col {display:grid;grid-template-columns: repeat(1,1fr);gap:0 1rem;}
/* .grid-sm {display:grid;grid-template-columns: repeat(1,1fr);gap:0 1rem;} */
.grid-span-2 {grid-column-end: span 2;}

.grid-col-2-right:nth-child(even) {
  text-align: right;
}

.grid-layout {
    display:grid; 
    min-height: 100vh;
    height: 100%;
    width: 100vw;
    grid-template-columns: 1fr;
    grid-template-rows: 100px 1fr 1fr 100px;
    justify-content: space-between;
}

.grid-preview-layout {
  min-height: auto;
  display:grid; 
  height: 100%;
  width: 100vw;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr ;
  justify-content: space-between;
}


.grid-gap-r1{row-gap: 1rem;}
.grid-gap-0{gap:0}

.grid-main-row-1fr-100 {grid-template-rows: 1fr 100px;}

.d-flex{display:flex}
.flex-wrap{flex-wrap: wrap;}
.d-space-between{justify-content: space-between;}
.flex-start{justify-content: flex-start;}


.w-auto{width:auto!important}
.w-auto{width:100%!important}
.mh-100{min-height: 100vh;}
.border { border: 1px solid black;}
.border-bottom-0 {border-bottom: none;}


.li-style-none{list-style-type: none;}
.li-inside{list-style-position: inside;}

.hidden {display:none;}

.btn {
  font-size: 1em;
  padding: 5px 10px; 
  border-radius: 5px;
  outline: none; 
  text-decoration: none;
  border: 1px solid transparent;
  color: white;
  background: red;
  cursor: pointer;
}
.callout-btn{
  background: purple;
  color: white;
}
.prev {
  background:rgb(73, 72, 72);
  color: white;
}
.next {
  background:red;
  color: white;
}

.btn:hover {
  box-shadow: inset 4px 4px 4px rgba(0,0,0,0.25);
}
